  box-shadow: 0 0 20px rgba(72, 98, 85, 0.6);
  box-sizing: border-box;
  padding: 40px;
  /* background-color: black; */
  text-align: center;
  /* border-style: double; */
  border: 7px double #000;
  /* display: grid;
  place-items: center; */
}
