.form-container p {
  text-align: center;
  letter-spacing: 1px;
  font-size: 45px;
  margin-bottom: 20px;
  color: #486255;
  font-weight: 400;
  /* color: #7d7e7e; */
}

.input,
.msg .area {
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
  margin-bottom: 25px;
  border: 2px solid #e9eaea;
  font-size: 14px;
  border-radius: 5px;
  outline: none;
  transform: all 0.5s ease;
}
.login .input {
  width: 49%;
  float: left;
}

.login .input:last-child {
  margin-left: 5px;
}

.msg .area {
  height: 200px;
}

.btn {
  width: 250px;
  background: #486255;
  /* background: #7d7e7e; */
  height: 50px;
  line-height: 40px;
  text-align: center;
  border-radius: 5px;
  margin: 0 auto;
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
}

.input:focus,
.msg .area:focus {
  border: 2px solid #486255;
}

::-webkit-input-placeholder {
  font-family: 'roboto';
}

#thank-you-container {
  margin-bottom: 1rem;
  color: blue;
  height: 20px;
}
#thank-you-container #thank-you {
  color: blue;
}

/* media screen */
@media screen and (max-width: 768px) {
  .contact-main {
    font-size: 15px;
  }
  .form-container {
    width: 400px;
    height: 655px;
  }
  .form-container p {
    font-size: 30px;
  }
  .input,
  .msg .area {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
    margin-bottom: 15px;
    border: 2px solid #e9eaea;
    font-size: 12px;
    border-radius: 5px;
    outline: none;
    transform: all 0.5s ease;
  }

  .btn {
    width: 160px;
    height: 40px;
    line-height: 15px;
    border-radius: 5px;
  }
  .login .input {
    width: 100%;
  }
}
